import React, { Component } from "react";
import "../Styles/css/Scroll.css";

import * as Scroll from "react-scroll";

class ScrollToTop extends Component {
  constructor(props) {
    super(props);
    this.scroll = Scroll.animateScroll;
  }
  render() {
    return (
      <div onClick={() => this.scroll.scrollToTop()} className="scroll-up">
        <span className="arrow-head icon-up-open" />{" "}
      </div>
    );
  }
}

export default ScrollToTop;



