import React, { Component } from "react";
import "../Styles/css/Sidebar.css";
import "../Styles/Fontello/css/fontello.css";
import { Link } from "react-router-dom";
import Home from "./Home";
import Logo from "../logo.svg";
class Sidebar extends Component {
  constructor(props) {
    super(props);
    const url = window.location.pathname
      .split("/")
      .filter(c => c.length > 0)[0];
    this.state = {
      activeCategory: typeof url === "undefined" ? "HOME" : url.toUpperCase(),
      logoHovered: false
    };
  }

  handleClick = evt => {
    const activeCategory = evt.target.getAttribute("data-name");
    this.setState({ activeCategory });
    //  this.props.updateActiveCategory(activeCategory);
  };

  render() {
    return (
      <nav>
        <div className="sidebar">
          <ul className="sidebar-list">
            <div className="sidebar-menu">
              <li className="sidebar-logo">
                <Link
                  onClick={this.handleClick}
                  className="logo-href"
                  to="/home"
                  data-name=""
                >
                  <img
                    className="logo-image"
                    style={{ width: "100%" }}
                    src={Logo}
                    alt="Logo of my website"
                  />
                </Link>
              </li>
              <li>
                <Link
                  className={
                    this.state.activeCategory === "HOME" ? "active" : ""
                  }
                  onClick={this.handleClick}
                  to="/home"
                  data-name="HOME"
                >
                  <span className="icon-home" />
                </Link>
              </li>
              <li>
                <Link
                  className={
                    this.state.activeCategory === "ABOUT" ? "active" : ""
                  }
                  onClick={this.handleClick}
                  to="/about"
                  data-name="ABOUT"
                >
                  <span className="icon-user" />
                </Link>
              </li>
              <li>
                <Link
                  className={
                    this.state.activeCategory === "SKILLS" ? "active" : ""
                  }
                  onClick={this.handleClick}
                  to="/skills"
                  data-name="SKILLS"
                >
                  <span className="icon-cog" />
                </Link>
              </li>
              <li>
                <Link
                  className={
                    this.state.activeCategory === "PROJECTS" ? "active" : ""
                  }
                  onClick={this.handleClick}
                  to="/projects"
                  data-name="PROJECTS"
                >
                  <span className="icon-folder-open" />
                </Link>
              </li>
              <li>
                <Link
                  className={
                    this.state.activeCategory === "CONTACT" ? "active" : ""
                  }
                  onClick={this.handleClick}
                  to="/contact"
                  data-name="CONTACT"
                >
                  <span className="icon-mail" />
                </Link>
              </li>
            </div>

            <div className="sidebar-social">
              <li className="sidebar-small">
                <a
                  href="https://www.linkedin.com/in/karol-gruszka-22a75a179/"
                  data-name="LinkedIn"
                >
                  <span className="icon-linkedin" />
                </a>
              </li>

              <li className="sidebar-small">
                <a href="#" data-name="CV">
                  <span className=" icon-down-circled2" />
                </a>
              </li>

              <li className="sidebar-small">
                <a href="https://github.com/Sniadekk" data-name="GitHub">
                  <span className="icon-github-circled" />
                </a>
              </li>
            </div>
          </ul>
        </div>
      </nav>
    );
  }
}

export default Sidebar;



