import React, { Component } from "react";
import "../Styles/css/Projects.css";
import { Link } from "react-router-dom";
import { projectsData } from "./Projects_data";
class Projects extends Component {
  constructor(props) {
    super(props);
    this.projects = Object.values(projectsData);
  }

  parseTechnologies = technologies => {
    return technologies.map((technology, index) => (
      <span className="core-technologies" key={`${index}-li`}>
        {technology}
      </span>
    ));
  };

  parseItems = () => {
    const url = window.location.origin;
    return this.projects.map(item => (
      <div key={item.name} className="projects-container-item">
        <div className="item-label">
          <span
            style={{ color: "#d3213f", fontSize: "0.8rem" }}
            className="icon-cancel-1"
          />
          <span className="item-name">{item.projectName.toUpperCase()}</span>
        </div>
        <Link to={`projects/${item.portfolioLink}`}>
          <img
            src={`${url}/Images/${item.portfolioLink}/avatar.png`}
            alt={item.description}
          />
        </Link>
      </div>
    ));
  };

  render() {
    return <div className="projects-container">{this.parseItems()}</div>;
  }
}

export default Projects;



