import React, { Component } from "react";
import "../Styles/css/Project.css";
import "../Styles/awesome-buttons/awb.css";
import { projectsData } from "./Projects_data";
import { Carousel } from "react-responsive-carousel";
import styles from "react-responsive-carousel/lib/styles/carousel.min.css";
class Project extends Component {
  constructor(props) {
    super(props);
    this.state = { activeProject: projectsData[props.match.params.project] };
  }

  parseImages = () => {
    const images = this.state.activeProject.images.map((image, index) => (
      <figure key={`${index}-figure`} className="project-figure">
        <div
          style={{
            backgroundImage: `url(${window.location.origin}/Images/${
              image.url
            })`
          }}
          className="project-image"
          alt={image.alt}
        />
      </figure>
    ));
    // dude, that's dirty
    if (this.state.activeProject.projectName === "przyjaznalapka.pl") {
      return [
        <iframe
          key="xxxxxxxiframe"
          src="https://www.youtube.com/embed/dIXB6IL_xUM"
          frameBorder="0"
          allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
          allowFullScreen="true"
        />
      ].concat(images);
    } else {
      return images;
    }
  };

  parseTechnologies = () => {
    return this.state.activeProject.technologies.map((technology, index) => (
      <li key={`${index}-li`} className="technology-block">
        {technology}
      </li>
    ));
  };

  parseDescription = () => {
    const str = this.state.activeProject.projectDescription;
    const result = str.match(/[^\.!\?]+[\.!\?]+/g);
    return result.map((sentence, index) => (
      <p key={`${index}-p`}>{sentence}</p>
    ));
  };

  render() {
    return (
      <div className="single_project-container">
        <h1 className="project-name">{this.state.activeProject.projectName}</h1>
        <h3 className="short-description">
          {this.state.activeProject.shortDescription}
        </h3>
        <div className="project-buttons">
          {this.state.activeProject.websiteLink && (
            <a
              href={this.state.activeProject.websiteLink}
              className="bttn-slant bttn-md bttn-block link-to-website"
            >
              VISIT THE WEBSITE
            </a>
          )}
          <a
            href={
              this.state.activeProject.githubLink
                ? this.state.activeProject.githubLink
                : "#"
            }
            className="bttn-slant bttn-md bttn-block link-to-github"
          >
            {this.state.activeProject.githubLink
              ? "SEE CODE"
              : "THE CODE CAN BE VIEWED ON SPECIAL REQUEST"}
          </a>
        </div>
        <Carousel
          swipeable={true}
          emulateTouch={true}
          showThumbs={false}
          useKeyboardArrows={true}
          showStatus={false}
          className="carousel-container"
        >
          {this.parseImages()}
        </Carousel>
        <h2 className="about-project">About this project</h2>
        <div className="project-description">{this.parseDescription()}</div>
        <h2 className="project-technologies-used">Technologies used</h2>
        <ul className="project-technologies">{this.parseTechnologies()}</ul>
      </div>
    );
  }
}

export default Project;



