import React, { Component } from "react";
import "../Styles/css/Home.css";
import ReactRevealText from "react-reveal-text";

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = { showFirst: false, showSecond: false, showThird: false };
    setTimeout(() => this.setState({ showFirst: true }), 200);
    setTimeout(() => this.setState({ showSecond: true }), 800);
    setTimeout(() => this.setState({ showThird: true }), 1400);
  }

  render() {
    return (
      <main>
        <div className="home-container">
          <div className="home-welcome-text">
            <ReactRevealText
              transitionTime={400}
              delayMax={150}
              show={this.state.showFirst}
              text={"Hello."}
            />
          </div>
          <div className="home-welcome-text home-welcome-text-name">
            <ReactRevealText
              transitionTime={400}
              delayMax={150}
              show={this.state.showSecond}
              text={"My name is Karol."}
            />
          </div>
          <div className="home-welcome-text-small">
            <ReactRevealText
              transitionTime={400}
              delayMax={150}
              show={this.state.showThird}
              text={"I'm here to create "}
            />
            <ReactRevealText
              transitionTime={400}
              className="home-welcome-text-color indent first"
              delayMax={150}
              show={this.state.showThird}
              text={"software"}
            />
            <ReactRevealText
              transitionTime={400}
              delayMax={150}
              className="indent second"
              show={this.state.showThird}
              text={"for"}
            />
            <ReactRevealText
              className="home-welcome-text-color indent third"
              transitionTime={400}
              delayMax={150}
              show={this.state.showThird}
              text={"you"}
            />
            <ReactRevealText
              className="home-welcome-text-dot"
              transitionTime={400}
              delayMax={150}
              show={this.state.showThird}
              text={"."}
            />
          </div>
        </div>
      </main>
    );
  }
}

export default Home;



